.loginPage {
  /* background-color: aqua; */
  background: url("https://previews.123rf.com/images/bondd/bondd1901/bondd190100034/114393148-homemade-sliced-cheesy-classic-italian-pizza-with-ingredients-and-olive-oil-white-marble-background-.jpg");
  background-size: cover;
  height: 100vh;
}

.loginPage > p {
  font-size: 70px;
  font-weight: bold;
  color: red;
  font-family: "Lobster", cursive;
}

label {
  color: black;
  font-size: 20px;
  font-weight: bold;
  /* text-align: left; */
}

input {
  background: white;
}

.loginContainer {
  position: relative;
  top: 40px;
  left: 40%;
  width: 250px;
  height: 250px;
  /* align-items: flex-start; */
  /* text-align: center; */
  display: flex;
  flex-direction: column;
  background: rgb(227, 230, 224);
  border: 1px solid;
  padding: 5px 0 5px 15px;
  border-radius: 10px;
  box-shadow: 5px 5px #888888;
}

form > button {
  margin-top: 15px;
  background: rgb(88, 88, 212);
  border-radius: 10px;
  height: 30px;
  width: 100px;
}

@media screen and (max-width: 768px) {
  .loginPage > p {
    font-size: 50px;
    font-weight: bold;
    color: green;
  }

  .loginContainer {
    position: relative;
    top: 60px;
    left: 30%;
    width: 250px;
    height: 250px;
    align-items: flex-start;
    text-align: center;
    display: flex;
    flex-direction: column;
    /* background: transparent;s */
    border: 2px solid;
    padding: 5px 0 5px 15px;
    border-radius: 10px;
  }
}
