h1 {
  /* color: white; */
  text-align: center;
  border-bottom: 4px solid yellow;
  width: 365px;
  font-style: italic;
  font-weight: bold;
  margin-left: 30%;
  position: relative;
  top: 85px;
}

.pizzaContainer {
  position: absolute;
  top: 140px;
}
.pizzaItems {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  overflow: hidden;
  /* width: 80%; */
  align-items: center;
  /* justify-content: center; */
  /* margin-right: 50px; */
  padding-left: 100px;
}
.pizzaContainer > h3 {
  text-align: left;
  margin-left: 100px;
  border-bottom: 2px solid;
}

@media screen and (max-width: 960px) {
  .pizzaItems {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 768px) {
  .pizzaContainer > h3 {
    text-align: center;
  }
  .pizzaItems {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 600px) {
  .pizzaItems {
    display: grid;
    grid-template-columns: 1fr;
    margin-left: 30px;
  }
  .h1 {
    margin-left: 25%;
  }
}
