.card > img {
  width: 250px;
  height: 250px;
  margin-left: 20px;
  margin-top: 5px;
  border-radius: 20px;
  object-fit: contain;
}

.card {
  margin: 0px 10px 10px 10px;
  border-bottom: 4px solid rgba(0, 0, 0, 0.4);
  border-right: 4px solid rgba(0, 0, 0, 0.4);
  border-radius: 15px;
  /* transform: scale(1.05); */
}
