.navBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin: 10px; */
  padding: 10px 30px 10px 50px;
  background-color: rgb(226, 166, 35);
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0;
}

.itemInCart {
  margin-right: 30px;
  border: 2px solid;
  padding: 0 5px;
  border-radius: 10px;
  background-color: turquoise;
  align-items: center;
  text-align: center;
}

.navBar_logo > img {
  width: 150px;
  height: 70px;
}
